@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:ital,wght@0,200;0,300;0,400;0,600;0,700;0,900;1,200;1,300;1,400;1,600;1,700;1,900&display=swap);
body{margin:0;font-family:-apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}
.AboutMe{display:flex;flex-direction:row;background-color:#12141f;padding-left:300px;position:relative;min-height:100vh;z-index:1}#aboutme-intro{margin-left:5%;margin-right:5%;margin-bottom:0;color:#a4acc4}#aboutme{color:#a4acc4;margin-left:5%;margin-right:5%;text-align:center}#container-img{height:auto;width:auto;overflow:hidden}#container-img img{max-width:100%;width:auto;height:auto;padding-top:15%;overflow:hidden}#name{text-align:center;font-size:calc(30px + 4vw);color:#fff;margin-top:0%;margin-left:5%;margin-right:5%;text-shadow:2px 2px 5px #141414}#container-options{list-style:none;align-content:center;padding-left:0;margin-left:5%;margin-right:5%;display:flex;justify-items:center;flex-direction:row;flex-wrap:wrap;justify-content:center}.AboutMe li{text-align:center;display:list-item;color:#000;width:20vh;background-color:#a4acc4;margin:1vh;padding:1vh;border-radius:5px;border:.5vh solid transparent;transition:border-color .2s ease-in-out;-webkit-transition:border-color .2s ease-in-out}.AboutMe li:hover{background-color:#4767c7}
body{margin:0;padding:0;font-family:sans-serif;background:beige}h1{text-align:center;color:#000}.skills-container{margin-top:2em;display:flex;overflow:hidden;flex-wrap:wrap}.circular-progress,.circular-progress::after,.circular-progress::before{width:200px;height:200px;border-radius:50%}.circular-progress{margin:auto;position:relative}.circular-progress::after,.circular-progress::before{position:absolute;top:0;left:0;box-sizing:border-box;text-align:center}.circular-progress::before{padding:.5em;line-height:140px;font-size:2rem;font-weight:bold}.circular-progress::after{font-size:.8rem;font-weight:normal;text-transform:uppercase;line-height:230px}.html{background:linear-gradient(0deg, #ff1aff 50%, rgba(255, 26, 255, 0.3) 50%);transform:rotate(72deg)}.html:before,.html:after{color:#ff1aff}.html:before{background:beige content-box;transform:rotate(-72deg);content:"40%"}.html:after{background:linear-gradient(transparent 50%, beige 50%);content:"html";transform:rotate(-72deg) scale(1.4)}.css{background:linear-gradient(0deg, #9999ff 50%, rgba(153, 153, 255, 0.3) 50%);transform:rotate(108deg)}.css:before,.css:after{color:#99f}.css:before{background:beige content-box;transform:rotate(-108deg);content:"60%"}.css:after{background:linear-gradient(transparent 50%, beige 50%);content:"css";transform:rotate(-108deg) scale(1.4)}
nav{align-items:center;align-content:center;display:block}.mi-header{position:fixed;align-items:center;left:0px;top:0px;transform:translateX(0px);height:100vh;width:300px;z-index:10;background:#191d2b;border-right:1px solid #2e344e;transition:all .4s ease-out 0s}.mi-header-inner{width:100%;height:100%;display:flex;flex-direction:column;align-items:center;overflow:hidden auto}.mi-header-image{display:block;width:100%;text-align:center;padding:20px 0px;border-bottom:1px solid #2e344e}.mi-header-image a{height:200px;width:200px;display:inline-block;vertical-align:middle;border-radius:1000px;border-width:7px;border-style:solid;border-color:#2e344e;border-image:none;border-image:initial;overflow:hidden}.mi-header-image img{max-width:100%;vertical-align:super;border-style:none}.mi-header-menu{width:100%;padding-left:0%;margin-top:0px;margin-bottom:1rem;display:block;list-style-type:disc;-webkit-margin-before:1em;margin-block-start:1em;-webkit-margin-after:1em;margin-block-end:1em;-webkit-margin-start:0px;margin-inline-start:0px;-webkit-margin-end:0px;margin-inline-end:0px}.mi-header-menu li{text-align:center;display:block;list-style:none}.mi-header-menu li a{padding:20px 0px;text-transform:uppercase;font-size:.94rem;letter-spacing:1px;display:block;color:#a4acc4;font-weight:600;position:relative;z-index:1;transition:all .4s ease-out 0s;overflow:hidden;background-color:transparent;text-decoration:none;box-sizing:border-box;margin:0px}.mi-header span{display:inline-block}
.App{background-color:#282c34;min-height:100vh;display:flex;font-family:"Source Sans Pro",sans-serif;font-size:calc(10px + 2vmin);color:#fff}
